@charset "UTF-8";
#main {
  background: var(--dark);
}
#main .header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: var(--primary);
}
#main .header__brand {
  font-family: var(--font-kufication);
}
#main .header__brand a {
  background: -webkit-linear-gradient(45deg, var(--light), var(--dark));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
#main .header__navbar ul li:not(:last-child) {
  margin-right: 1.875em;
}
#main .header__navbar ul li a {
  font-family: var(--font-titillium-regular);
  font-size: 1em;
  letter-spacing: 0.0625em;
  text-transform: uppercase;
  color: var(--light);
}

:root {
  --font-signature: "Signature";
  --font-spotyfied: "Spotyfied";
  --font-galaxus: "Galaxus";
  --font-mangaka: "Mangaka";
  --font-kufication: "Kufication";
  --font-memesique: "Memesique";
  --font-titillium-regular: "Titillium Regular";
  --font-titillium-light: "Titillium Light";
  --font-titillium-light-italic: "Titillium Light Italic";
  --font-titillium-bold: "Titillium Bold";
  --font-titillium-semibold: "Titillium Semi Bold";
  --primary: #528ce4;
  --light: #f8f9fa;
  --grey: #6c757d;
  --orange: #ffc107;
  --danger: #dc3545;
  --dark: #212529;
  --primary-background: linear-gradient(225deg, #ffd89b 10%, #19547b 70%);
  --secondary-background: linear-gradient(45deg, #ffd89b 10%, #19547b 70%);
  --focus-background: linear-gradient(45deg, #ffd89b 10%, #dc3545 70%);
  --btn-hover-boxshadow: rgba(0, 0, 0, 0.099) 6px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) 6px 8px 8px 0px, rgba(0, 0, 0, 0.099) -6px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) -6px 8px 8px 0px;
  --btn-focus-boxshadow: rgba(0, 0, 0, 0.099) 0px 8px 8px 0px,
    rgba(0, 0, 0, 0.099) 0px 8px 8px 0px;
  --card-boxshadow: rgba(0, 0, 0, 0.4) 0px 30px 90px;
  --box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

::-webkit-scrollbar,
::-webkit-scrollbar-thumb,
::-webkit-scrollbar-track {
  display: none;
}

*::selection {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
}

* {
  border: none;
  outline: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.2;
}

a,
a:active a:visited {
  text-decoration: none;
}

/* ------------------ custom cursor ------------------ */
.cursor {
  position: fixed;
  top: -40px;
  left: -40px;
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: difference;
}
.cursor::before {
  content: "";
  width: 80px;
  height: 80px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: var(--light);
  border: 1px solid var(--light);
  transition: all ease 0.15s;
  transform: scale(0.15);
}

.cursor.light::before {
  transform: scale(1);
  background: transparent;
}

.cursor.drag {
  mix-blend-mode: unset;
}
.cursor.drag::before {
  transform: scale(1);
  background: var(--primary);
  border-color: transparent;
  content: "î¨£";
  font-family: "LineIcons";
  font-size: 20px;
  color: #fff;
}

/* ------------------ preloader ------------------ */
.preloader {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background: var(--dark);
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 9;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
  transition-delay: 1s;
  transition-delay: 1s;
}
.preloader .loadbar {
  width: 0%;
  height: 100%;
  max-height: 6px;
  background: var(--primary);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  overflow: hidden;
}
.preloader figure {
  width: 200px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fade 0.3s;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
  position: relative;
}
.preloader figure span {
  width: 100%;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  color: var(--light);
  text-align: center;
  font-size: 14px;
}
.preloader figure img {
  height: 100px;
  display: inline-block;
  opacity: 0.05;
}

@keyframes fade {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
/* ------------------ page loaded ------------------ */
.page-loaded .preloader {
  height: 0;
}
.page-loaded .preloader .loadbar {
  max-height: 100%;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
}
.page-loaded .preloader figure {
  transform: translateY(50px);
  opacity: 0;
}

/* ------------------ custom button ------------------ */
.custom-btn {
  display: inline-block;
  padding: 0.625em 1.25em;
  background: var(--dark);
}
.custom-btn a {
  font-family: var(--font-galaxus);
  font-size: 1.375em;
  line-height: 0.5;
  letter-spacing: 0.0625em;
  color: var(--light);
}

